<template>
    <v-container class="py-16">
            <h3 class="text-h3 mb-4 text-center">Aktualności</h3>
            <v-row class="py-8">
                <v-col cols="12" lg="4" v-for="(action, index) in actions" :key="index">
                    <v-card shaped elevation="24">
                        <v-img v-if="action.image" aspect-ratio="1.5" :src="action.src" :lazy-src="action.lazy"/>
                        <v-card-title class="headline">{{action.title}}</v-card-title>
                        <v-card-subtitle>{{action.date}}</v-card-subtitle>
                                <v-card-text>{{action.text}}</v-card-text>
                                <v-card-actions v-if="action.href">
                                    <v-btn text small>czytaj dalej...</v-btn>
                                </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
            <v-row class="justify-center">
                    <v-btn outlined rounded color="info">Więcej</v-btn>
            </v-row>
    </v-container>
</template>

<script>
import news from '../db/news.json'

export default {
    name: 'news',
    data: () => ({
        news: news.news,
        more: false,
        isVisibleArticle: false,
        isVisibleArticle2: false,
        windowWidth: 0
    }),
    mounted(){
        this.checkWidth()
    },
    computed: {
        actions(){
            let actionsFiltred = []
            let length = 3
            if(this.news.length < 3){
                length = this.news.length
            }
            for (let index = 0; index < length; index++) {
                var position = this.news.length-(index+1)
                var action = this.news[position]
                actionsFiltred.push(action)
            }
            return actionsFiltred
        }
    },
    methods: {
        checkWidth(){
            this.windowWidth = window.innerWidth;
        },
        onIntersectArticle(entries){
            if(!this.isVisibleArticle){
                this.isVisibleArticle = entries[0].isIntersecting
            }
        },

        onIntersectArticle2(entries){
            if(!this.isVisibleArticle2){
                this.isVisibleArticle2 = entries[0].isIntersecting
            }
        }
    }
}
</script>